:root {
   --main-transition: 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.header {
    position: relative;
    z-index: 20;
  }
  
  .container-mb-menu {
    position: absolute;
    z-index: 10;
    // top: 0px;
    // left: 0;
    width: 100%;
    height: 100%;
  
    @media screen and (min-width: 768px) {
      display: none;
    }
  }
  
  .mobile-menu {
    position: relative;
    top: 0;
    left: 0;
    width: 100%;
  
    max-width: 375px;
    min-width: 320px;
    min-height: 800px;
  
    height: 100vh;
    padding-top: 84px;
    background-image: url('../images/mob-menu@1x.png'),
      radial-gradient(
         183.6% 183.01% at 59.76% -23.42%, 
         #4F2EE8 18.03%,
         #FFFFFF 100%);
    background-position: bottom;
    background-size: contain;
    background-repeat: no-repeat;
  
    @media screen and (min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
      background-image: url('../images/mob-menu@2x.png'),
        radial-gradient(
          183.6% 183.01% at 59.76% -23.42%,
          #4f2ee8 18.03%,
          #ffffff 100%
        );
    }
  }
  
  .signup-btn {
    display: flex;
    align-items: center;
    justify-content: space-between;
    min-width: 184px;
    margin-top: 20px;
    padding: 13px 14px;
  
    background-color: #4f2ee8;
    color: #FFFFFF;
    border: 1.5px solid #111;
    border-radius: 18px;
    cursor: pointer;

    &:hover,
    &:focus {
      background-color: #ffffff;
      color: #4f2ee8;
      transition: color var(--main-transition),
                background-color var(--main-transition);
     
    }
  }
  
  .signup-btn-text {
    font-family: 'DM Sans';
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 18px;
    letter-spacing: -0.04em;
  }
  
  .user-container {
    display: flex;
    align-items: center;
    margin-bottom: 130px;
    gap: 9px;
    
  }

  .user-icon {
   
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .user-name {
    font-weight: 700;
    font-size: 22px;
    line-height: 29px;
    letter-spacing: -0.02em;
    color: #FFFFFF;
    
  }
  
  .mobile-list {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

//   .display-none {
//     display: none;
//   }

  .mobile-home,
  .mobile-shoplist {
    display: inline-flex;
    padding: 8px 16px;
    margin-left: -16px;
    text-decoration-line: none;
  }

  .mobile-home {
    background: #EAC645;
    border-radius: 36px;
    font-weight: 700;
    font-size: 14px;
    line-height: 18px;
    letter-spacing: -0.04em;
    text-transform: uppercase;
    color: #111111;
  }
  
  .mobile-home-item:not(:last-child) {
    margin-bottom: 9px;
  }
  
  .mobile-shoplist {
    align-items: start;
    font-weight: 500;
    font-size: 14px;
    line-height: 18px;
    letter-spacing: -0.04em;
    text-transform: uppercase;
    color: #FFFFFF;

  }
  
  .shoplist-icon {
    fill: #ffffff;
    padding-left: 4px;
  }
  
  .mobile-logout {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 4px;
    position: absolute;
    width: 118px;
    height: 48px;
    left: 20px;
    bottom: 20px;
    font-size: 14px;
    line-height: 18px;
    letter-spacing: -0.04em;
    color: #111111;

    border-radius: 18px;
    cursor: pointer;
  
    &:hover,
    &:focus {
      background-color: #4f2ee8;
      color: #ffffff;
      transition: color var(--main-transition),
      background-color var(--main-transition);

      
     
    }
  }
  
